<template lang="html">
  <Panel title="金融头条" :class="$style.panel">
    <section :class="$style.content">
      <swiper :options="swiperOption">
        <swiper-slide>百万券免费用，速来领取!!<em>></em></swiper-slide>
        <swiper-slide>签到领流量，1元500M!<em>></em></swiper-slide>
      </swiper>
    </section>
  </Panel>
</template>

<script>
import Panel from "../core/panel.vue"
import {swiper,swiperSlide} from 'vue-awesome-swiper'
export default {
  components: {
    Panel,
    swiper,
    swiperSlide,
  },
  data() {
    return {
      swiperOption: {
        direction: 'vertical',
        autoplay: {
            delay: 2500,
            disableOnInteraction: false
          },
      }
    }
  }
}
</script>
<style lang="scss">
  .swiper-container-vertical{
    height: 72px;
    .swiper-slide{
      font-size: 28px;
      color: #333;
      em{
        float: right;
        color: #ddd;
        margin-right: 20px;
      }
    }

  }
</style>
<style lang="scss" module>
@import "../../css/element.scss";
.panel {
  @include panel;
  margin-top: 120px !important;
  >h4{
    display: none;
  }
  .content{
    height: 72px;
    line-height: 72px;
    overflow: hidden;
    padding-left: 166px;
  }
}
</style>



