<template lang="html">
  <Panel title="每日签到" :class="$style.panel">
    <section :class="$style.content">
      <div :class="$style.item"  v-for="item in items" :key="item.price">
        <img :src="item.catory" alt="">
        <section :class="$style.list">
          <div :class="$style.product" v-for="prod in item.list" :key="prod.price">
            <img :src="prod.img" alt="">
            <p :class="$style.title">{{prod.title}}</p>
            <p :class="$style.price"><em>{{prod.price}}</em>起</p>
            <article>
              <div :class="$style.wrapper">
                <div :class="$style.inner" :style="{width:prod.progress+'%'}">

                </div>
              </div>
              <p>{{prod.progress}}%</p>
            </article>
            <label v-if="prod.label!=''">{{prod.label}}</label>
          </div>
        </section>
      </div>
    </section>
  </Panel>
</template>

<script>
import Panel from "../core/panel.vue"
export default {
  components:{
    Panel,
  },
  data(){
    return {
      items:[{
        catory:"//img12.360buyimg.com/jrpmobile/jfs/t3406/210/2247820735/50952/86016eea/5846d8e7N20446dbd.png",
        list:[{
          img:"//img30.360buyimg.com/cf/jfs/t1/42015/29/627/71619/5cc2fd15E066af381/6494a4fb153c63e8.jpg!q70.dpg",
          title:"阳光下枣香的味道",
          price:"56.00",
          progress:"4",
          label:"精选"
        },{
          img:"//img30.360buyimg.com/cf/jfs/t1/30233/7/14973/47456/5cbfc6a3E669f6413/3e08b18f94426888.jpg!q70.dpg",
          title:"多用洗涤剂",
          price:"49.00",
          progress:"131",
          label:""
        }]
      },{
        catory:"//img12.360buyimg.com/jrpmobile/jfs/t3406/210/2247820735/50952/86016eea/5846d8e7N20446dbd.png",
        list:[{
          img:"//img30.360buyimg.com/cf/jfs/t29767/336/1182664629/41775/fe3c584/5cd8ff45Ncd79c5c6.jpg!q70.dpg",
          title:"时髦防晒有一套",
          price:"29.00",
          progress:"0",
          label:""
        },{
          img:"//img30.360buyimg.com/cf/jfs/t1/34554/10/6713/54025/5cc552d5Edef8b9c3/dc5b4d66408ba5dd.jpg!q70.dpg",
          title:"天才知音国学机",
          price:"388.00",
          progress:"103",
          label:""
        }]
      },{
        catory:"//img12.360buyimg.com/jrpmobile/jfs/t3406/210/2247820735/50952/86016eea/5846d8e7N20446dbd.png",
        list:[{
          img:"//img30.360buyimg.com/cf/jfs/t1/41370/9/3803/44710/5cd00b83Eaf89b30b/2898238caf3ac234.jpg!q70.dpg",
          title:"儿童运动头盔",
          price:"68.00",
          progress:"44",
          label:""
        },{
          img:"//img30.360buyimg.com/cf/jfs/t1/40862/31/1777/32387/5cc7bf7dE1edafc1d/4f39ab34e01f81bd.jpg!q70.dpg",
          title:"折叠风扇",
          price:"58.00",
          progress:"1",
          label:""
        }]
      }]
    }
  }
}
</script>

<style lang="scss" module>
@import "../../css/element.scss";
.panel{
  @include panel;
  .content{
    @include flex;
    .item{
      margin-bottom: 40px;
      >img{
        display: block;
        width: 100%;
        height: 170px;
      }
      .list{
        @include flex(row);
        .product{
          width: 50%;
          height: 396px;
          box-sizing: border-box;
          background: rgba(0,0,0,0.02);
          text-align: center;
          position: relative;
          img{
            width: 180px;
            height: 180px;
            margin: 44px auto 20px;
          }
          .title{
            font-size: 28px;
            color: #444;
          }
          .price{
            color: #999;
            font-size: 20px;
            line-height: 48px;
            height: 48px;
            em{
              font-size: 36px;
              color: #ff3232;
              margin-right: 4px;
              &:before{
                content: "￥";
                font-size: 28px;
              }
            }
          }
          article{
            @include flex(row);
            align-items: center;
            justify-content: space-around;
            padding: 0 20px;
            height: 30px;
            line-height: 30px;
            margin-top: 16px;
            .wrapper{
              width: 144px;
              height: 10px;
              background: #f3f5f7;
              border-radius: 22px;
              .inner{
                width: 40%;
                max-width: 100%;
                height: 10px;
                background-image: linear-gradient(270deg,#f55 1%,#ff9c31);
                border-radius: 22px;
              }
            }
            p{
              font-size: 24px;
              color: #999;
            }
          }
          label{
            position: absolute;
            left:0;
            top: 0;
            font-size: 28px;
            color: #ff0000;
            padding: 4px 8px;
            text-align: center;
            background-image: linear-gradient(-35deg,#c1c1c1 0,#acacac 100%);
          }
        }
      }
    }
  }
}

</style>



