<template lang="html">
  <Panel title="下载APP" :class="$style.panel">
    <section :class="$style.content">
      <div class=""></div>
      <p>立即下载</p>
    </section>
  </Panel>
</template>

<script>
import Panel from "../core/panel.vue"
export default {
  components:{
    Panel,
  },
}
</script>

<style lang="scss" module>
@import "../../css/element.scss";
html,body{
  background: #fff;
}
.panel{
  @include panel;
  margin: 0 !important;
  >h4{
    display: none;
  }
  .content{
    @include flex;
    justify-content: center;
    align-items: center;
    height: 1136px;
    >div{
      background: url(https://gw.alicdn.com/mt/TB1wDdrgf6H8KJjSspmXXb2WXXa-750-1334.png) no-repeat;
      width: 600px;
      height: 800px;
      background-size: 100% auto;
      margin-top: -400px;
    }
    >p{
      @include btn($size:32px,$color:#fff,$bgcolor:#F1443E,$padding:5px,$radius:5px);
      height: 100px;
      width: 471px;
      line-height: 100px;
      position: fixed;
      bottom: 130px;
    }
  }
}
</style>



