<template lang="html">
  <section :class="cname">
    <swiper :options="options">
      <swiper-slide v-for="item in items" :key="item.href">
        <router-link :to="{ name: item.href}">
          <img :src="item.src" alt="">
        </router-link>
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
  </section>

</template>

<script>
import {swiper,swiperSlide} from 'vue-awesome-swiper'
export default {
  props: {
    options: {
      type: Object,
      default() {
        return {
          autoplay: true,
          loop: true,
          pagination: {
            el: ".swiper-pagination",
            clickable: true,
          },
          notNextTick: false,
        }
      },
    },
    items: {
      type: Array,
      default() {
        return []
      }
    },
    cname: {
      type: String,
      defaule: '',
    }
  },
  components: {
    swiper,
    swiperSlide,
  }
}
</script>

<style lang="scss">
@import "~swiper/dist/css/swiper.css";
</style>



