<template lang="html">
  <Panel title="商品推荐" :class="$style.panel">
    <section :class="$style.content">
      <div :class="$style.arrow">
        更多尖货 >
      </div>
      <Slider :options="options" :items="items" cname="product-slider"></Slider>
    </section>
  </Panel>
</template>

<script>
import Panel from '../core/panel.vue';
import Slider from '../core/slider.vue';
export default {
  components: {
    Panel,
    Slider,
  },
  data() {
    return {
      items:[
        {
          href: "home",
          src: "https://img12.360buyimg.com/jrpmobile/jfs/t1/24464/26/8171/23261/5c73b40dEddca0b4d/d23341c0ec8932e9.jpg?width=210&height=260",
        },{
          href: "home",
          src: "https://img12.360buyimg.com/jrpmobile/jfs/t1/19379/16/8191/19539/5c73b4aaEb1b3cb27/f2566275b974eae8.jpg?width=210&height=260",
        },{
          href: "home",
          src: "https://img12.360buyimg.com/jrpmobile/jfs/t1/19123/13/8083/20016/5c73b514E0bd4cac9/a2e2b5da65c36580.jpg?width=210&height=260",
        },{
          href: "home",
          src: "https://img12.360buyimg.com/jrpmobile/jfs/t1/22706/31/8158/20065/5c73b4d1Ecd7b7948/eb412d04d9e5a9c3.jpg?width=210&height=260",
        }
      ],
      options: {
        slidesPerView: 2.3,
        spaceBetween: 30,
        freeMode: true
      }
    }
  }
}
</script>

<style lang="scss">
.product-slider{
  .swiper-container{
    box-sizing: border-box;
    padding: 0 24px;
    .swiper-slide {
      a{
        display: inline-block;
        width: 100%;
        img{
          width: 100%;
          display: block;
          height: 314px;
          border: 1px solid #fafafa;
        }
      }
    }
  }
}
</style>
<style lang="scss" module>
  @import "../../css/element.scss";
  .panel {
    @include panel;
    .content {
      padding-bottom: 40px;
      position: relative;
      .arrow {
        position: absolute;
        font-size: 28px;
        color: #999;
        right:20px;
        top: -70px;
      }
    }
  }
</style>



