<template lang="html">
  <Panel title="新手特权" :class="$style.panel">
    <section :class="$style.content">
      <div :class="$style.item">
        <h4>下载APP<span :class="$style.red">送888元礼包</span></h4>
        <p :class="$style.gray">新手专享</p>
        <img src="//img10.360buyimg.com/mcmktadmin/jfs/t1/27454/32/12048/7347/5c94bdd0E6e9d9a25/611102ce5816adb0.png?width=88&height=88" alt="">
      </div>
      <div :class="$style.item">
        <ul>
          <li>
            <img src="//img10.360buyimg.com/mcmktadmin/jfs/t1/27454/32/12048/7347/5c94bdd0E6e9d9a25/611102ce5816adb0.png?width=88&height=88" alt="">
            <h4 :class="$style.red">领128元新手礼包</h4>
            <p :class="$style.gray">立即开通</p>
          </li>
          <li>
            <img src="//img10.360buyimg.com/mcmktadmin/jfs/t1/27454/32/12048/7347/5c94bdd0E6e9d9a25/611102ce5816adb0.png?width=88&height=88" alt="">
            <h4>打白条<span>享免息</span></h4>
            <p :class="$style.gray">立即开通享豪礼</p>
          </li>
        </ul>
      </div>
    </section>
  </Panel>
</template>

<script>
import Panel from "../core/panel.vue";
export default {
  components: {
    Panel,
  },
  data () {
    return {

    }
  }
}
</script>

<style lang="scss" module>
@import "../../css/element.scss";
.panel {
  @include panel;
  >h4{
    border-bottom: 1px solid #ddd;
  }
  .content {
    @include list(row);
    .item {
      width: 50%;
      box-sizing: border-box;
      &:first-child{
        padding: 32px 20px;
        text-align: center;
        border-right: 1px solid #ddd;
        img{
          width: 132px;
          height: 132px;
          margin: 20px auto 0;
        }
      }
      h4{
        font-size: 26px;
        line-height: 40px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        text-align: left;
      }
      .red {
        color: red;
      }
      .gray{
        color: #999;
        font-size: 24px;
      }
      p{
        margin-top: 6px;
        font-size: 24px;
      }
      ul{
        width: 100%;
        li{
          height: 144px;
          width: 100%;
          padding: 32px 28px;
          box-sizing: border-box;
          &:first-child{
            border-bottom: 1px solid #ddd;
          }
          img{
            width: 80px;
            height: 80px;
            float: right;
          }
        }
      }
    }
  }
}

</style>



